/**
 * Author(s) : Adheesh
 * Purpose : This component lists down the events planned per day
 */
import styles from './EventDetail.module.scss';
import { COMMONS } from '../../constants';
import PropTypes from "prop-types";
import EventTime from '../common/eventTime/EventTime';

const EventDetails = ({events}) => {
  let eventData = [];
  events.forEach((data) => {
    let event = [];
    data.info.forEach((eventsPerday) => {
      event.push(
        <div key={eventsPerday.title} className={styles.event}>
          <EventTime startTime={eventsPerday.startTime} endTime={eventsPerday.endTime}/>
          <div className={styles.eventTitle}>{eventsPerday.title}</div>
        </div>
      );
    });
    eventData.push(
      <div className={styles.eventsPerDay} key={data.date}>
        <div className={styles.eventDate}>{data.date}</div>
        <div className={styles.eventTimeline}>{event}</div>
      </div>
    );
  });

  return (
    <div className={styles.eventDetailsContainer}>
      <div className={styles.eventDetailsHeader}>
        <span className={styles.title}>{COMMONS.EVENTS}</span>
      </div>
      <div className={styles.eventDetailsContent}>{eventData}</div>
    </div>
  );
};

export default EventDetails;
EventDetails.propTypes = {
  events : PropTypes.array
}

