import React, { useEffect, useRef } from 'react';
import AgendaListUI from '../../components/agendaListUI/AgendaListUI';
import Calender from '../../components/calendar/Calendar';
import EventDetails from '../../components/eventDetail/EventDetail';
import PageTitle from '../../components/pageTitle/PageTitle';
import { PAGE_TITLE_CONSTANT, TIME_NOTE } from '../../constants';
import styles from '../SchedulePage/SchedulePage.module.scss';

export default function SchedulePage(props) {
  const { events, agenda } = props;
  const agendaListRef = useRef(null);

  const handleDateSelection = (index) => {
    agendaListRef.current.scrollToAgenda(index);
  };

  const schedulePageRef = useRef(null);
  useEffect(()=>{
    schedulePageRef.current.scrollIntoView({block: 'start'});
  });

  return (
    <div ref={schedulePageRef} className={styles.scheduleWrapper}>
      <PageTitle label={PAGE_TITLE_CONSTANT.SCHEDULE} />
      <p className={styles.note}>{TIME_NOTE.note}</p>
      <div className={styles.containerWrapper}>
        <div className={styles.leftContainer}>
          <Calender handleDateSelection={handleDateSelection} />
          <EventDetails className={styles.eventDetails} events={events} />
        </div>
        <div className={styles.rightContainer}>
          <AgendaListUI ref={agendaListRef} agenda={agenda} />
        </div>
      </div>
    </div>
  );
}
