import React from 'react';
import styles from './SessionDetails.module.scss';
import EventTime from '../../common/eventTime/EventTime';
import data from '../../../assets/data/allspark.json';

const SessionDetails = (props) => {
  const {
    sessionDetails,
    speakersID = [],
  } = props;
  const { title, startTime, endTime, url = '', screen = '' } = sessionDetails;
  const speakers = data.speakers;
  return (
    <>
      <div className={styles.sessionInfoLeftContainer}>
        {url === '' ? (
          <div className={styles.sessionTitle}>{title}</div>
        ) : (
          <a href={url} target="blank">
            <div className={`${styles.link} ${styles.sessionTitle}`}>
              {title}
            </div>
          </a>
        )}
        <EventTime
          startTime={startTime}
          endTime={endTime}
          screen={screen}
          category ={sessionDetails.category !== 'break' ? sessionDetails.category : '' }
          repeat = {sessionDetails.repeat}
        />
      </div>
      <div className={styles.sessionInfoRightContainer}>
        {speakersID && (
          <div className={styles.sessionSpeakersContainer}>
            {speakersID.map((speakerInfo, index) => {
              const { thumbnail, name } = speakers[speakerInfo] || {};
              const img = require(`../../../assets/images/${thumbnail}`);

              return (    
                  <img src={img} alt={name} key={index}/>
              );
            })}
          </div>
        )}
      </div>
    </>
  );
};

export default SessionDetails;
