/**
 * Author(s) : Shakthi Priya
 * Purpose : To show Event info details when the agenda is hovered and clicked in the tooltip
 */
import styles from './EventInfo.module.scss';
import React from 'react';
import { EventInfoConstants, TimeConstants } from '../../constants';
import PropTypes from 'prop-types';
import data from '../../assets/data/allspark.json';

const EventInfo = (props) => {
  const { speakers_ID, sessionDetails} = props;
  const speakers = data.speakers;

  return (
    <div className={styles.eventInfoContainer} >
      <div className={styles.theme}></div>
      <div className={styles.eventInfo}>
      {sessionDetails.url === '' ? (
          <div className={styles.eventTitle}>{sessionDetails.title}</div>
        ) : (
          <a href={sessionDetails.url} target="blank">
            <div className= {styles.eventTitle}>
              {sessionDetails.title}
            </div>
          </a>
        )}
        <div className={styles.eventCategory}>{sessionDetails.category}</div>
        <div
          className={
            styles.eventDuration
          }>{`${sessionDetails.startTime} - ${sessionDetails.endTime} (${sessionDetails.duration} ${TimeConstants.minutes})`}</div>
          {sessionDetails.screen ?
          <div className={styles.screen}>{sessionDetails.screen}</div>
          : null}
        {speakers_ID.length !== 0 && (
          <div className={styles.speakers}>{EventInfoConstants.presenters}</div>
        )}
        {speakers_ID &&
          speakers_ID.map((speaker,speakerIndex) => {
            const {thumbnail,name}=speakers[speaker] || {};
            const img=require(`../../assets/images/${thumbnail}`);
            return (
              <div className={styles.speaker} key={speakerIndex}>
                <div className={styles.speakerImageContainer}>
                  <img
                    className={styles.speakerImage}
                    src={img}
                    alt={name}></img>
                </div>
                <div className={styles.speakerName}>
                  {name}
                </div>
              </div>
            );
          })}
      </div>
    </div>
  );
};

EventInfo.propTypes = {
  speakers_ID: PropTypes.array,
  sessionDetails: PropTypes.object,
  eventInfo: PropTypes.object,
  isEventInfoOpen: PropTypes.bool,
  setEventDetailOpen: PropTypes.func
};

export default EventInfo;
