import React from 'react';
import { PropTypes } from "prop-types";
import styles from "./CalendarItem.module.scss";

export default function CalendarItems({ items ,isFade}) {
  return (
    <div className={styles.calendarItems}>
      {items.map((item,index) => (
          <div key={index} className = {isFade === 1 ? item >= 25 && item <= 30  ? styles.faded  : styles.notFaded : styles.notFaded 
          && isFade === 6 ? item >= 1 && item <= 5  ? styles.faded  : styles.notFaded : styles.notFaded
         }>{item}
          </div>
      ))}
    </div>
  );
}

CalendarItems.propTypes = {
  date: PropTypes.string,
};