
/**
 * Author(s) : Karthik
 * Purpose : This coponent is used to display AgendaList inside the schedule page.
 */
import React, {
  useRef,
  createRef,
  forwardRef,
  useImperativeHandle,
} from 'react';
import styles from './AgendaListUI.module.scss';
import data from '../../assets/data/allspark.json';
import AgendaDateUI from './agendaDateUI/AgendaDateUI';
import AgendaSessions from './agendaSessions/AgendaSessions';

const AgendaListUI = forwardRef((props, ref) => {
  const agendaList = data.agenda;
  const myRefs = useRef([]);
  myRefs.current = agendaList.map(
    (element, index) => myRefs.current[index] ?? createRef()
  );

  useImperativeHandle(ref, () => ({
    scrollToAgenda(index) {
      myRefs.current[index].current.scrollIntoView({ behavior: 'smooth' });
    },
  }));

  return (
    <div className={styles.agendaListContainer}>
      {agendaList.map((agenda, index) => {
        const { day, date, fullDate, info } = agenda;
        return (
            <div
              className={styles.agendaContainer}
              key={index}
              ref={myRefs.current[index]}>
              <AgendaDateUI day={day} date={date} fullDate={fullDate} />
              <div className={styles.agendaRightContainer}>
                {info.map((sessionsInfo, sessionsInfoIndex) => {
                  return (
                    <>
                      {sessionsInfo.length > 1 ? (
                        <div
                          className={styles.sameTimelineSessions}
                          key={sessionsInfoIndex}>
                          <AgendaSessions
                            sessionsInfo={sessionsInfo}
                            data={data}
                            singleSession={false}
                          />
                        </div>
                      ) : (
                        <div key={sessionsInfoIndex}>
                          <AgendaSessions
                            sessionsInfo={sessionsInfo}
                            data={data}
                            singleSession={true}
                          />
                        </div>
                      )}
                    </>
                  );
                })}
              </div>
            </div>
        );
      })}
    </div>
  );
});

export default AgendaListUI;
