import React from 'react'
import CalendarEventDates from '../calendarEventDate/CalendarEventDate'
import CalendarItems from '../calendarItem/CalendarItem'
import CalendarTitle from '../calendarMonth/CalendarMonth'
import CALENDARDATE_CONSTANTS from '../../constants/calendar-constants'
import styles from "./Calendar.module.scss"

export default function Calender(props) {
    const { handleDateSelection } = props;
    return (
      <div className={styles.calendarWrapper}>
        <CalendarTitle />
        {CALENDARDATE_CONSTANTS.map((items, index) =>
          <CalendarItems key={index} items={items} isFade={index === 1 || index === CALENDARDATE_CONSTANTS.length - 1 ? index : 0} />
        )}
        <CalendarEventDates handleDateSelection={handleDateSelection} />
      </div>
    );
  }