import React,{forwardRef} from 'react';
import styles from './SessionInfo.module.scss';
import SessionDetails from './sessionDetails/SessionDetails';

const SessionInfo = forwardRef((props , ref) => {
  const { sessionDetails, speakersID = [], singleSession = true } = props;
  const { category = '' } = sessionDetails;

  return (
    <>
      {category === 'break' ? (
        <div className={`${styles.sessionInfoContainer} ${styles.break}`}>
          <SessionDetails
            sessionDetails={sessionDetails}
            speakersID={speakersID}
            singleSession={singleSession}
          />
        </div>
      ) : (
        <div className={`${styles.sessionInfoContainer} session-detail-card`}  ref={ref}>
          <SessionDetails
            sessionDetails={sessionDetails}
            speakersID={speakersID}
            singleSession={singleSession}
          />
        </div>
      )}
    </>
  );
});

export default SessionInfo;
