import React, { useRef } from 'react';
import { useState } from 'react';
import styles from "./CalendarEventDate.module.scss";

export default function CalendarEventDates(props) {

  const [isSelected, setSelected] = useState(false);
  const ref = useRef([]);
  const { handleDateSelection } = props;

  const toggleEventDates = [19, 20];


  const toggleClass = (e, index) => {
    handleDateSelection(index);
    setSelected(!isSelected);
    for (let i of toggleEventDates) {
      if (i === parseInt(e)) {
        ref.current[i].className = styles.selectedDate;
      }
      else {
        ref.current[i].className = "";
      }
    }
  };

  return (
    <div className={styles.eventDates}>
      <div className={styles.selectedDate}
        onClick={e => toggleClass(e.target.getAttribute("value"), 0)} value="19" ref={(element) => { ref.current[19] = element; }} id="19">19</div>
      <div
        onClick={e => toggleClass(e.target.getAttribute("value"), 1)} value="20" ref={(element) => { ref.current[20] = element; }} id="20">20</div>
      {/* <div 
              onClick={e=>toggleClass(e.target.getAttribute("value"))}  value = "21" ref={(element) => {ref.current[21] = element;}}  id = "21">21</div> */}
    </div>
  );
}
